/* Fonts */

@include fontGenerator("Poppins Light",(300),(poppins-light));
@include fontGenerator("Poppins Regular",(400),(poppins-regular));
@include fontGenerator("Poppins Medium",(500),(poppins-medium));
@include fontGenerator("Poppins Semibold",(600),(poppins-semibold));
@include fontGenerator("Poppins Bold",(700),(poppins-bold));

@mixin font-300 {
  font-family: "Poppins Light", sans-serif;
 }  // font-weight: 300

@mixin font-400 {
  font-family: "Poppins Regular", sans-serif;
 }  // font-weight: 400

@mixin font-500 {
  font-family: "Poppins Medium", sans-serif;
 }  // font-weight: 500

@mixin font-600 {
  font-family: "Poppins Semibold", sans-serif;
 }  // font-weight: 600

@mixin font-700 {
  font-family: "Poppins Bold", sans-serif;
 }  // font-weight: 700
